@import url(https://fonts.googleapis.com/css?family=Roboto&display=swap);
.input-wrap {
    position: relative;
    margin-bottom: 20px;
    width: 350px;
}
.input-wrap label {
    font-size: .8rem;
    padding-left: .3rem;
    margin-bottom: 1rem;
}

.input-wrap input {
    width: 340px;
    height: 40px;
    border-radius: 5px;
    box-shadow: none;
    border: 1px solid rgb(26, 25, 25);
    padding: 5px;
}

.input-wrap input:focus {
    border: 1px solid rgb(61, 149, 250);
    font-size: 1.1rem;
}
.center {
    width: 21rem;
    height: 30rem;
    position: absolute;
	top:0;
	bottom: 0;
	left: 0;
	right: 0;
    margin: auto;

}
.center img {
    padding-left: 4rem;
}
.center p {
    text-align: center;
}
.login-text {
    margin-bottom: 2rem;
    margin-top: 1rem;
}
#submit-btn {
    font-size: 1rem;
    text-transform: uppercase;
    width: 351px;
    height: 50px;
    border: none;
    border-radius: 5px;
    background-color: rgb(61, 149, 250);
    color: #fff;
}
#submit-btn:hover {
    background-color: rgb(74, 157, 251);
}

form p {
    margin-top: 1rem;
}
form p a{
    text-decoration: none;
    color: rgb(61, 149, 250);
}
.navbar-container {
    display: flex;
    justify-content: space-between;
}
.navbar-container .logo {
    margin-left: 4rem;
    cursor: pointer;
}

.auths {
    margin-right: 4rem;
    margin-top: 1.5rem;
}
.auths a {
    margin-right: 2rem;
}

#login-link {
    text-decoration: none;
    font-weight: 800;
    font-size: .8rem;
    color: #000000;
}
#login-link:hover {
    color: #78a2da;
}
#signup-link {
    text-decoration: none;
    font-weight: 800;
    font-size: .8rem;
    border: 1px solid #000000;
    padding: .7rem;
    color: #000000;
    -webkit-transition: all ease 0.3s;
    transition: all ease 0.3s;
}
#signup-link:hover {
    color: #fff;
    border: 1px solid #78a2da;
    background-color: #78a2da;
}

.fixed-sidebar, .layout-body{
    display: inline-block;
    vertical-align: top;
    height : calc(100vh - 67px);
    overflow: auto;
    
}
.fixed-sidebar{
    width: 20%;
    background-color: rgb(226, 226, 226);
    margin-right: 40px;
}
.layout-body{
    width: 68%;
    padding: 10px;

}
.admin-layout-header{
    padding: 10px;
    text-align: center;
    background-color: rgb(90, 128, 118);
    font-weight: bold;
    margin-bottom: 10px;
}
.fixed-sidebar a{
    text-decoration: none;
    padding: 10px;
    display: block;


}
.card-container {
    width: 265px;
    margin-top: 50px;
    cursor: pointer;
}
.card-container .name {
    font-weight: bolder;
    font-size: 20px;
    color: black;
    text-decoration: none;
}

.card-container .description {
    margin-top: 10px;
    margin-bottom: 10px;
    font-weight: 500;
    font-size: 15px;
    color: rgb(35, 35, 35);
    text-decoration: none;
}
.card-container .email,
.card-container .phone {
    font-size: 12px;
    width: 100%;
    color: black;
    text-decoration: none;
}
.layout-body .container{
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
}
#heading {
    margin-bottom: 20px;
}
#email-description {
    border-radius: 5px;
    box-shadow: none;
    border: 1px solid rgb(26, 25, 25);
    padding: 5px;
    height: 70px;
}
#email-description:focus {
    border: 1px solid rgb(61, 149, 250);
}
.modal .modal-main {
    display: flex;
    flex-direction: row;
}
.second-div {
    margin-left: 10px;
    margin-top: 40px;
    width: 36%;
}
.second-div .modal-name {
    margin-bottom: 10px;
    font-weight: bolder;
    font-size: large;
}
.contact-div {
    margin: 70px 2rem 0 10px;
}
.phone, .email, .webUrl {
    text-decoration: none;
    margin-bottom: 40px;
}

.closeModal-btn {
    height: 2rem;
    position: absolute;
    width: 2rem;
    right: 0;
    font-size: 9px;
    font-size: 22px;
}
.header {
    height: 54vh;
    background-image: -webkit-gradient(
        linear,
        left top, right bottom,
        from(rgba(61, 149, 250, 0.082)),
        to(rgba(14, 59, 110, 0.938))),
        url(/static/media/homeCover.3f2564f4.jpg);
    background-image: linear-gradient(
        to right bottom,
        rgba(61, 149, 250, 0.082),
        rgba(14, 59, 110, 0.938)),
        url(/static/media/homeCover.3f2564f4.jpg);
    background-size: cover;
    background-position: top;
    position: relative;
}
.header .header-container {
    width: 70vw;
    text-align: center;
    position: absolute;
	top: 8rem;
	bottom: 0;
	left: 0;
	right: 0;
    margin: auto;
}
.header h1 {
    color: #ffffff;
    font-size: 2rem;
    font-weight: bolder;
    line-height: 1.1;
}

.search_form {
    margin-top: 4rem;
}

.search_form input {
    height: 44px;
    width: 65%;
    border: none;
    padding: 1rem;
}

.search_form button {
    border: none;
    height: 44px;
    background-color: #0ed085;
    color: #fff;
    border-radius: 0;
    width: 30%;
    text-transform: uppercase;
    position: relative;
    transition: all ease 0.3s;
    -webkit-transition: all ease 0.3s;
    -moz-transition: all ease 0.3s;
    -o-transition: all ease 0.3s;
    -ms-transition: all ease 0.3s;
}
.search_form button:hover, .search_form button:focus, .search_form button:active, .search_form button:visited {
    background-color: rgba(14, 208, 134, 0.807);
    transition: all ease 0.3s;
    -webkit-transition: all ease 0.3s;
    -moz-transition: all ease 0.3s;
    -o-transition: all ease 0.3s;
    -ms-transition: all ease 0.3s;
}

.main-body {
    display: flex;
    justify-content: space-between;
    flex-wrap: wrap;
    padding:0 10% 0 10% ;
}
.modal-container {
    width: 100vw;
    height: 100vh;
}
.modal {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background: rgba(0, 0, 0, 0.6);
}
.modal .modal-main {
    position: fixed;
    background: white;
    width: 80%;
    height: auto;
    top: 50%;
    left: 50%;
    -webkit-transform: translate(-50%, -50%);
            transform: translate(-50%, -50%);
}

.display-block {
    display: block;
}

.display-none {
    display: none;
}
*,
*::after,
*::before  {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}
body {
    font: 400 13px/20px 'Source Sans Pro', sans-serif;

}

p {

}

input:focus {
    outline: none;
}

.bold {
    font-weight: 800;
    font-size: 2rem;
}
